header {
    width: 100%;
    height: 100vh;
    position: relative;

    #topbar {
        position: fixed;
        top: 0;
        left: 0;
        background-color: #000;
        color: #fff;
        padding: 25px 0;
        z-index: 10000;
    }

    #topslider {
        width: 100%;
        height: 100%;
        background-color: brown;

        .carousel, .carousel-inner, .carousel-item {
            height: 100%;
        }
    }

    ul.menu {
        list-style-type: none;
        margin: 0;
        padding: 0;
        overflow: hidden;

        li {
            & > a {
                display: block;
                color: white;
                text-align: center;
                text-decoration: none;
            }
        }

        &.h {
            li {
                float: left;

                & > a {
                    padding: 15px 25px;
                }

                &:hover {
                    & > a {
                        color: rgb(10, 171, 245);
                    }
                }
            }
        }

        &.v {

        }
    }
}